


































import Vue from 'vue'
import { Component } from 'vue-property-decorator'
import { BookDto } from '@/services/models.ts'

@Component
export default class AddBookModal extends Vue {
  resolve!: Function
  reject!: Function

  book = new BookDto()

  modal = false
  public open(): Promise<BookDto> {
    this.modal = true
    return new Promise((resolve, reject) => {
      this.resolve = resolve
      this.reject = reject
    })
  }

  accept() {
    this.resolve(this.book)
    this.modal = false
    this.book = new BookDto()
  }

  decline() {
    this.resolve(new BookDto())
    this.modal = false
    this.book = new BookDto()
  }
}
