import { VuexModule, Module, Mutation, getModule } from 'vuex-module-decorators'
import store from '@/store'
import { BookDto } from '@/services/models'
import { Action } from 'vuex-module-decorators'
import api from '@/services/api'

export interface BookState {
  books: Array<BookDto>
  loading: boolean
}

@Module({ store, dynamic: true, name: 'book' })
class BookModule extends VuexModule implements BookState {
  books: Array<BookDto> = new Array<BookDto>()
  loading = false

  public get getBooks() {
    return this.books
  }

  @Mutation
  public updateBooks(payload: Array<BookDto>) {
    this.books = payload
  }

  @Mutation
  private updateLoading(payload: boolean) {
    this.loading = payload
  }

  @Action({ rawError: true })
  public async loadBooks() {
    if (this.loading) return
    console.log('its working')

    const books = await api.books.getAll()
    this.updateBooks(books)
    this.updateLoading(true)
  }

  @Action({ rawError: true })
  public async updateList() {
    const books = await api.books.getAll()
    this.updateBooks(books)
  }
}

export const bookModule = getModule(BookModule)
