




































import Vue from 'vue'
import { Component, Ref } from 'vue-property-decorator'
import api from '@/services/api.ts'
import { BookDto } from '@/services/models.ts'

import AddBookModal from '@/pages/books/AddBookModal.vue'
import ConfirmModal from '@/pages/component/ConfirmModal.vue'
import { bookModule } from '@/store/modules/bookModule'

@Component({
  components: {
    AddBookModal,
    ConfirmModal
  }
})
export default class Books extends Vue {
  @Ref('addbookModal') readonly addbookModal!: AddBookModal
  @Ref('confirmModal') readonly confirmModal!: ConfirmModal

  headers = [
    { text: 'Название', value: 'title' },
    { text: '', value: 'actions', align: 'right', sortable: false }
  ]

  items: Array<BookDto> = new Array<BookDto>()

  async addbook() {
    const result = await this.addbookModal.open()
    if (result.title && result.text) {
      const book = new BookDto()
      book.title = result.title
      book.text = result.text
      await api.books.add(book)
      await this.updateList
    }
  }

  editItem(id: string) {
    this.$router.push({
      name: 'book',
      params: { id }
    })
  }

  async deleteItem(id: number) {
    const result = await this.confirmModal.open()

    if (result) {
      await api.books.delete(id)
      await this.updateList()
    }
  }

  async updateList() {
    await bookModule.updateList()
    this.items = bookModule.getBooks
  }

  async created() {
    await bookModule.loadBooks()
    this.items = bookModule.getBooks
  }
}
